import React from 'react';
import './categories.css';

const categories = [
  { name: "Women's", path: "/Women's" },
  { name: "Men's", path: "/Men's" },
  { name: 'Kids', path: '/Kids' },
  { name: 'Phones', path: '/Phones' },
  { name: 'TV', path: '/Tv' },
  { name: 'Games', path: '/Games' },
];

const Categories = () => {
  return (
    <div className="categories">
      <ul>
        {categories.map((cat, index) => (
          <li key={index}>
            <a href={cat.path} className="linkscatg">
              {cat.name}
              <span className="icon">
                <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" fill="currentColor" viewBox="0 0 16 16">
                  <path fillRule="evenodd" d="M4 8a.5.5 0 0 1 .5-.5h5.793L8.146 5.354a.5.5 0 1 1 
                  .708-.708l3 3a.5.5 0 0 1 0 .708l-3 3a.5.5 0 0 1-.708-.708L10.293 
                  8.5H4.5A.5.5 0 0 1 4 8"/>
                </svg>
              </span>
            </a>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default Categories;
